import React                          from 'react';
import PropTypes                      from 'prop-types';

import { generateId, buildClassName } from '../utils';
import styles                         from './tooltip.css';
import IconButton                     from '../IconButton';

const Tooltip = ( {
    children,
    className,
    cssMap,
    id = generateId( 'Tooltip' ),
    isDismissible,
    message,
    onClickClose,
    onMouseOut,
    onMouseOver,
    position,
    role,
} ) => (
    <div
        className = { buildClassName( className, cssMap, {
            dismissible : isDismissible,
            position,
            role,
        } ) }
        onMouseEnter = { onMouseOver }
        onMouseLeave = { onMouseOut }>
        <div
            className = { cssMap.tooltip }
            id        = { id }
            role      = "tooltip">
            <div className = { cssMap.message }>
                { message || children }
            </div>
            { isDismissible &&
                <IconButton
                    className = { cssMap.close }
                    iconSize  = "S"
                    iconTheme = "button"
                    iconType  = "close"
                    label     = "Close"
                    onClick   = { onClickClose } />
            }
        </div>
    </div>
);

Tooltip.propTypes =
{
    /**
     * Extra CSS class name
     */
    className     : PropTypes.string,
    /**
     *  CSS class map
     */
    cssMap        : PropTypes.objectOf( PropTypes.string ),
    /**
     * HTML id attribute
     */
    id            : PropTypes.string,
    /**
     *  Display the tooltip as user dismissible
     */
    isDismissible : PropTypes.bool,
    /**
     *  Tooltip message text (string or JSX)
     */
    message       : PropTypes.node,
    /**
     *  Function to call on “Close” button click: ( e ) => { ... }
     */
    onClickClose  : PropTypes.func,
    /**
     *  onMouseOver callback function: ( e ) => { ... }
     */
    onMouseOver   : PropTypes.func,
    /**
     *  onMouseOut callback function: ( e ) => { ... }
     */
    onMouseOut    : PropTypes.func,
    /**
     *  Tooltip position relative to associated component
     */
    position      : PropTypes.oneOf( [
        'top',
        'topLeft',
        'topRight',
        'bottom',
        'bottomLeft',
        'bottomRight',
        'left',
        'leftTop',
        'leftBottom',
        'right',
        'rightTop',
        'rightBottom',
    ] ),
    /**
     *  Tooltip role/style
     */
    role : PropTypes.oneOf( [ 'default', 'critical', 'promoted', 'warning' ] ),
};

Tooltip.defaultProps =
{
    className     : undefined,
    cssMap        : styles,
    id            : undefined,
    isDismissible : undefined,
    message       : undefined,
    onClickClose  : undefined,
    onMouseOut    : undefined,
    onMouseOver   : undefined,
    position      : 'top',
    role          : 'default',
};

Tooltip.didWarn = {};

export default Tooltip;
